// Colors

$color-default-black: #000000;
$color-default-white: #ffffff;
$color-dark: #2d383f;
$color-light: #ffffff;
$color-bright: #0266c1;
$color-transparent: rgba(255, 255, 255, 0);

// Gradient

$black-to-right: linear-gradient(
  90deg,
  rgba(0, 0, 0, 0.75) 0%,
  rgba(0, 0, 0, 0) 75%
);

// Typography
$font-regular: "Montserrat", "Arial", sans-serif;
$font-semi-bold: "Montserrat Semi Bold", "Arial", sans-serif;
$font-bold: "Montserrat Bold", "Arial", sans-serif;
$font-extra-bold: "Montserrat Extra Bold", "Arial", sans-serif;
$font-light: "Montserrat Light", "Arial", sans-serif;

// переменная используемая в html для подключения скейлинга
$fz-default: 16px;
$ff-placeholder: "Placeholder", "Arial", sans-serif;
$ff-rouble: "Rouble", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default: ease;
$trans-default: 0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal: 0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600: 0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320: 320px;
$vp-375: 375px;
$vp-768: 768px;
$vp-1024: 1024px;
$vp-1200: 1200px;
$vp-1440: 1440px;
$vp-1920: 1920px;

// Retina
// ---------------------------------

$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;
